/* Animations */
@keyframes fadeUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* General Styles */
body {
  background-image: url('img/bg2.jpg');
  background-size: cover;
  background-position: center;
  margin: 0;
  font-family: 'Lato', sans-serif;
  height: 100vh;
}

/*  Container */
.experience-container {
  padding: 70px 20px;
  text-align: center;
  min-height: 100vh;
  box-sizing: border-box;
  animation: fadeUp 1s ease-out forwards;
}

/* Heading Secion */
.heading-heading {
  font-family: "Righteous", sans-serif;
  color:#000000;
}

.heading-heading p {
  font-family: "Righteous", sans-serif;
  color:#000000;
}


/* Category Toggle Secion */
.category-toggle {
  margin-top: 40px;
  margin-bottom: 40px;
}

.toggle-button {
  background-color: white;
  color: #ff7f00;
  padding: 15px 30px;
  font-size: 1.2rem;
  border: none;
  margin: 0 15px;
  cursor: pointer;
  font-family: "Righteous", sans-serif;
  transition: background-color 0.3s ease, color 0.3s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}

.toggle-button.active {
  background: linear-gradient(180deg, #ff3333, #4b0082, #00008b, #000000);
  color: white;
}

/* Projects Secion */
.projects-section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.projects-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

.project-card {
  background: linear-gradient(180deg, #ff3333, #4b0082, #00008b, #000000);
  padding: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.project-card img,
.project-card video {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.project-card h3 {
  color: #00ff00;
  font-size: 1.4rem;
  margin: 20px 0;
}

@media (min-width: 768px) {
  .projects-container {
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
  }
}

@media (max-width: 768px) {
  .projects-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 480px) {
  .projects-container {
    grid-template-columns: 1fr;
  }
}

/* Modal Viewing Secion */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 20px;
}

.modal-content {
  background: linear-gradient(180deg, #ff3333, #4b0082, #00008b, #000000);
  max-width: 90%;
  width: 900px;
  max-height: 90%;
  padding: 20px;
  text-align: center;
  color: white;
  overflow: auto;
  animation: fadeUp 1s ease-in-out;
}

.modal-content img {
  width: 100%;
  height: auto;
  max-height: 100%;
  object-fit: cover;
}

.modal-content h3 {
  margin-top: 20px;
  color: #00ff00;
  font-size: 1.6rem;
}

.modal-content p {
  font-size: 1rem;
  margin-top: 20px;
  line-height: 1.5;
}

.close-btn {
  background-color: #ff4500;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
}

.close-btn:hover {
  background-color: #ff6347;
}

.modal-content video {
  width: 100%;
  height: auto;
  max-height: 100%;
}

/* Skills Section */
.skills-section {
  padding: 20px 0;
  text-align: center;
}

.skills-section ul {
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}

.skill-card {
  background: linear-gradient(180deg, #ff3333, #4b0082, #00008b, #000000);
  padding: 20px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  border-radius: 0; /* Ensure no corner radius */
  cursor: default;
  pointer-events: none; 
}

.skill-icon {
  width: 100%;
  height: auto;
  max-height: 60px;
  object-fit: contain;
}

@media (max-width: 768px) {
  .skill-card {
    width: 60px;
    height: 60px;
  }

  .skill-icon {
    max-height: 40px;
  }
}

/* Footer Section */
.footer-section {
  padding: 20px 20px;
  text-align: center;
}

.footer-section .social-links {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.footer-section .social-links a {
  padding: 12px 20px;
  border-radius: 50%;
  background-color: #ffffff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.footer-section .social-links a:hover {
  background-color: #f0f0f0;
  transform: translateY(-3px);
}

.footer-section .social-links a img {
  width: 20px;
  height: auto;
}